<template>
	<!-- 老师消息详情 -->
	<view class="teacher flex_columns">
		<view class="choice flex-around flex-center">
			<text>今天日期：{{data}}</text>
			<text>选择查看天数:{{array[selectDay]}}</text>
			<picker @change="bindPickerChange" :value="selectDay" :range="array"><text class="logout">选择</text></picker>
		</view>
		<view class="tea_one flex_columns">
			<view class="tit_bk flex-center">
				<text class="tit_m">序号</text>
				<text class="tit_m">姓名</text>
				<text class="tit_l">图片</text>
				<text class="tit_l">体温值</text>
				<text class="tit_m">状态</text>
				<text class="tit_m">进出</text>
			</view>
			<view class="tit_list flex-center" v-for="(item ,index) in newslist.list" :key='index' v-if="newslist">
				<text class="tit_m">{{index+1}}</text>
				<text class="tit_m">{{item.reserveFour}}</text>
				<image :src="item.imgbase64" mode="aspectFill" @click="maximg(item.imgbase64)"></image>
				<!-- <text class="tit_xxl">{{item.reserveThree}}</text> -->
				<text class="tit_l">{{item.temperature}}</text>
				<text class="tit_m" v-if="item.temperaturestate=='1'">正常</text>
				<text class="tit_m" v-if="item.temperaturestate=='2'">异常</text>
				<text class="tit_m" v-if="item.reserveTwo=='1'">进入</text>
				<text class="tit_m" v-if="item.reserveTwo=='2'">出入</text>
			</view>
			<view class="tit_page flex-center" v-if="newslist">
				<view class="pages_number flex-center flex-around">
					<paging :pageSize="pageSize" :total="total" activecolor="#FFFFFF" activebackground="#DD524D" :footer="true"
					 :current="1" @changes="add"></paging>
				</view>
			</view>
			<view class="null" v-if="!newslist">
				<text>暂无记录</text>
			</view>
		</view>
		<cusPreviewImg ref="cusPreviewImg" :list="ImgList" />
	</view>
</template>

<script>
	import paging from '../../../components/yang-paging/fy.vue';
	import cusPreviewImg from '@/components/cus-previewImg/cus-previewImg.vue';
	export default {
		components: {
			paging,
			cusPreviewImg
		},
		data() {
			return {
				newslist: '',
				ImgList: [],//图片预览
				userlist: '',
				data:this.utils.getDate(),
				selectDay: 0, //天数
				array: ['今天', '一天前', '二天前', '三天前', '四天前', '五天前', '六天前'],
				pageNum: 1, //第几页
				pages: 0, //总页数
				pageSize: 0, //每页数
				total: 0, //数据总条数
				navigatepageNums: 0
			}
		},
		onLoad(e) {
			this.userlist = uni.getStorageSync('userlist'); //加载用户缓存
			if(e.status==0){
				uni.removeStorageSync('newslist');
			}else{
				this.userlist = uni.getStorageSync('userlist'); //加载用户缓存
				this.newslist = uni.getStorageSync('newslist'); //加载用户缓存
				this.pageNum = this.newslist.pageNum;
				this.pages = this.newslist.pages;
				this.pageSize = this.newslist.pageSize;
				this.total = this.newslist.total;
				console.log(this.userlist);
				console.log(this.newslist);
			}
		},
		methods: {
			bindPickerChange: function(e) {
				this.utils.showloading();
				console.log('picker发送选择改变，携带值为', e.target.value)
				this.selectDay = e.target.value;
				this.pageNum = 1;
				this.teacher();
			},
			//图片放大
			maximg(item) {
				this.ImgList = [item];
				this.$refs.cusPreviewImg.open(this.ImgList);
			},
			//页码渲染
			add(index) {
				this.utils.showloading();
				this.pageNum = index;
				this.teacher();
			},
			//老师请求
			teacher() {
				this.http.getApi('/discern/ClassDate', {
					schoolId: this.userlist.schoolId,
					classId: this.userlist.classId,
					selectDay: this.selectDay,
					// selectDay: 5,
					pageNum: this.pageNum,
					pageSize: 10
				}, 'post').then(res => {
					console.log("res");
					console.log(res);
					uni.hideLoading();
					if (res.data.userDate.list.length == 0) {
						this.newslist = '';
						this.utils.error('暂无更多');
					} else {
						this.newslist = res.data.userDate;
						this.pageNum = res.data.userDate.pageNum;
						this.navigatepageNums = res.data.userDate.navigatepageNums;
						this.total = res.data.userDate.total;
						this.pageSize = res.data.userDate.pageSize;
					}
				}).catch(err => {
					console.log("err");
					console.log(err);
					uni.hideLoading();
					this.newslist = '';
					console.log(this.newslist);
				});
			},
		}
	}
</script>

<style lang="scss">
	//通用
	.logout {
		padding: 5px 10px;
		margin-right: 20px;
		color: #FFFFFF;
		/* box-shadow: 1px 2px 6px #F55866; */
		box-shadow: 1px 2px 6px #DAC1E7;
		/* background: linear-gradient(45deg,#c779d0,#4bc0c8); */
		background: linear-gradient(45deg, rgba(254, 172, 94, 0.5), rgba(199, 121, 208, 0.5), rgba(75, 192, 200, 0.5));
		border-radius: 10px;
		font-size: 15px;
		font-weight: 700;
	}

	//表格尺寸宽度
	.tit_s {
		width: 20px;
	}

	.tit_m {
		width: 30px;
	}

	.tit_l {
		width: 40px;
	}

	.tit_xl {
		width: 50px;
	}

	.tit_xxl {
		width: 60px;
	}

	.choice {
		padding: 10px 0;

		text {
			font-size: 13px;
			margin: 0 10px;
		}
	}

	.teacher {
		max-width: 100%;
		min-height: 750px;
		padding: 0 15px;
		background-color: #f8f8f8;
	}

	.tea_one {
		width: 100%;
		// margin-top: 30px;
		border-radius: 10px;
		background-color: #FFFFFF;

		//标题
		.tit_bk,
		.tit_list,
		.tit_page {
			width: 100%;
			// border: #f8f8f8 1px solid;
			text {
				// height: 100%;
				min-width: 20px;
				color: #333333;
				font-size: 11px;
				padding: 3px 3px;
				// border: #DAC1E7 1px solid;
			}
		}

		.tit_bk {
			background-color: #DD524D;
			border-radius: 10px 10px 0 0;
			color: #FFFFFF;

			text {
				padding-top: 5px;
				padding-bottom: 5px;
				color: #FFFFFF;
				border-right: #FFFFFF 1px solid;
			}

		}

		.tit_list {
			border-bottom: #C8C7CC 1px solid;

			text {
				border-right: #C8C7CC 1px solid;
			}

			image {
				padding: 3px;
				width: 40px;
				height: 40px;
				border-radius: 15px;
			}
		}

		//页码
		.tit_page {
			justify-content: center;

			.pages_tit {
				padding: 0 5px;
				font-size: 12px;
				border-radius: 5px;
				border: #999999 1px solid;
				justify-content: center;
			}

			.pages_number {
				padding: 5px 10px;

				// min-width: 100%;
				text {
					border: #999999 1px solid;
					padding: 5px 15px;
					font-size: 12px;
					margin-right: 10px;
					border-radius: 5px;
				}

				text:hover {
					background-color: #DD524D;
					color: #FFFFFF;
				}
			}

			.pages_tit:hover {
				background-color: #DD524D;
				color: #FFFFFF;
			}
		}
	}
</style>
